.postsWrapper{
    margin: 0 3%;
}
h3{
    margin-bottom: 10px;
}
.addPost textarea{
    width: 50%;
    border-radius: 5px;
    margin-right: 5%;
    resize: none;
}
.addPost button{
    width: 15%;
    height: 30px;
    border: none;
    background-color: #a3a3a3;
    border-radius: 5px;
    cursor: pointer;
}
.addPost{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}