.navBar {
    grid-area: n;
    background-color: #d8e0df;
    padding: 20px;
}
.item a {
    text-decoration: none;
    color: #070707;
    font-size: 20px;
}
.item{
margin: 10px 0 0 0;
}
.item a.active{
    color: #6c6c6c;
    margin: 20px;
}
.nav{
    margin-bottom: 70px;
}


.friend{
    width: 100%;
    max-width: 50px;
    min-width: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
}
.friend img{
    border-radius: 50%;
    width: 100%;
}
.friend p{
    /*min-font-size: 10px;*/
    font-size: 14px;
    font-family: 'Gilroy-light';
    text-decoration: none;
}
.friend{
    text-decoration: none;

}
.friends{
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
    gap: 10px;
}

