.dialogs {
    display: grid;
    grid-template: 1fr / 2fr 5fr;
    gap: 20px;
}

.userAvatar{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 5px;
}

.item{
    color: #070707;
}

.dialog a{
    margin: 20px 0 20px 10px;
    text-decoration: none;
    display: flex;
    align-items: center;
}

.messages textarea{

    resize: none;
    width: 70%;
}
.addMessages{
    display: flex;
    align-items: center;
    margin-top: 30px;
}

.message {

}

.dialogsItems {

}

.dialogsItems .active{
    color: #6c6c6c;
    margin-left: 30px;
}
