* {
    margin: 0;
    padding: 0;
    /*font-family: 'Gilroy-regular' 'Gilroy-bold'  'Gilroy-light'  '' ;*/
    font-family: 'Gilroy-medium';
}

li {
    list-style-type: none;
}

.app-wrapper {
    width: 80%;
    margin: 0 auto;
    display: grid;
    grid-template: "h h" 60px "n c" 1fr / 4fr 10fr;
    gap: 20px;
}

.app-wrapper-content {
    grid-area: c;
    background-color: #D8E0DF;
}




